/**
 * @description
 * Lookup for different payment statues
 * Each object has the following properties:
 * icon: the type of icon, primary | warning | success | danger
 * name: name is used as a key for translation | i18n
 *
 * @name paymentStatusLookup
 * @type {Object}
 */
const paymentStatusLookup = {
  ENTERED: {
    icon: 'primary',
    name: 'entered',
  },

  READY: {
    icon: 'primary',
    name: 'ready',
  },

  ACCEPTED: {
    icon: 'warning',
    name: 'accepted',
  },

  APPROVED: {
    icon: 'success',
    name: 'approved',
  },

  PROCESSED: {
    icon: 'success',
    name: 'processed',
  },

  REJECTED: {
    icon: 'danger',
    name: 'rejected',
  },

  CANCELLED: {
    icon: 'warning',
    name: 'cancelled',
  },

  CANCELLATION_PENDING: {
    icon: 'warning',
    name: 'cancellation-pending',
  },
};

export default paymentStatusLookup;



